.banner__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
}

.banner__text {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}

.banner {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;

  min-height: 480px;
}

.banner__picture {
  text-align: center;
}

.banner__buttom {
  text-align: center;
  width: 280px;
}

.banner__button-wrap {
  margin: 0;
  width: 280px;


  bottom: 0;

  a {
    color: white;
  }
}

@media (min-width: $tablet-width) {
  .banner {
    align-items: flex-start;
  }

  .banner__title {
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    text-align: left;
  }

  .banner__text {
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
  }

  .banner__button-wrap {
    left: 0;
    top: 300px;
  }
}

@media (min-width: $desktop-width) {
  .banner {
    width: 1330px;
  }
}
